import React, {Fragment} from 'react';
import ReactDOM from 'react-dom';
import Footer from './components/layout/Footer';
import Header from './components/layout/Header';
import Main from './components/layout/Main';
import NavMiddle from './components/layout/NavMiddle';
import NavMiddleMenu from './components/itens/NavMiddleMenu';
import NavTop from './components/layout/NavTop';

import './index.css';

ReactDOM.render(
  <Fragment>

    <NavTop/> {/* Navegao do topo */}
    <Header/> {/* Ttulon subttulo e data atual da pgina */}
    <NavMiddle/> {/* Filtro de notcias */}
    <NavMiddleMenu/> {/* S  exibido para mobile */}
    <Main/> {/* Area principal com as notcias */}
    <Footer/> {/* Rodap da pgina */}

  </Fragment>,
  document.getElementById('root')
);

console.log(`
  .:Ol:.

  * Tecnologias usadas:
    - HTML, CSS, Javascript e React.

  * Objetivo da pgina:
    - Simular um site de notcias responsivo componentizado.

  * Funcionalidades Objetivas e tcnicas:
    - Simular um site de notcias online responsivo com diversas funcionalidades separadas por componentes;
    - O site  criado separando cada parte do layout (Header, main, footer...) em componentes;
    - Dentro de cada componente layout, h outros componentes como notcias, botes e links;
    - Dentro tambm de cada um desses componentes h outros menores que representam cones e coisas semelhantes;
    - O componente de notcia recebe um texto e uma imagem via props e, ento, os renderiza na tela como notcia;
    - As notcias so separadas entre Principais e Comuns. As Principais so as duas primeiras que aparecem no incio, e o restante so as notcias Comuns;
    - As notcias Principais recebem classes e estilos diferentes para darem um destaque mais coerente com o grau de importncia da notcia;
    - As notcias Comuns so feitas usando o mesmo componente da notcia Principal, mas so organizadas em display 'grid' dentro menos importncia que as duas primeiras chamadas Princiapais;
    - Os cones/assuntos que aparecem no topo de cada notcia NEM SEMPRE  coerente com o assunto. s vezes s est ali para indicar que eles existem;
    - H um scroll-x para a rea de colunistas quando o tamanho da tela comea a se aproximar do formato mobile.

`);


